.root {
  height: 140px;
  align-items: center;
  padding-left: 100px;
  display: flex;
  padding-right: 45px;
  justify-content: space-between;

  .social_container {
    display: flex;
    gap: 1.5rem;
    width: fit-content;

    svg {
      font-size: 1.75rem;
    }
  }

  .copyright {
    font-size: 0.875rem;
    color: #cdd5df;
  }

  .logo {
    filter: grayscale(100%);
    width: 150px;
    height: 70px;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 500ms;

    &:hover {
      filter: grayscale(0%);
    }
  }

  @media screen and (max-width: 850px) {
    padding-left: 50px;
    padding-right: 1rem;

    .social_container {
      gap: 1rem;

      svg {
        font-size: 1.5rem;
      }
    }

    .copyright {
      font-size: 0.75rem;
    }

    .logo {
      width: 100px;
      height: 40px;
    }
  }

  @media screen and (max-width: 550px) {
    .copyright {
      font-size: 10px;
      margin-right: 40px;
    }

    .logo {
      display: none;
    }
  }

  @media screen and (max-width: 450px) {
    flex-direction: column;
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 0px;

    .copyright {
      font-size: 0.75rem;
      margin-right: 0px;
    }
  }
}
