.root {
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
  padding-left: 1.25rem;
  padding-right: 1.25rem;

  h5 {
    background-color: transparent;
    font-size: 1.26rem;
    font-weight: 700;
  }

  p {
    font-size: 12px;
    color: #e1dee6;
    background-color: transparent;
    border-radius: 0.5rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    line-height: 1rem;
    font-weight: 500;
    text-align: center;
  }

  .login_info {
    display: flex;
    background-color: transparent;

    span {
      background-color: transparent;
      font-size: 12px;
      margin-left: 0.125rem;
      margin-right: 0.125rem;
    }

    span:nth-child(2n) {
      color: #b692f6;
      font-weight: 500;
    }
  }

  @media screen and (max-width: 500px) {
    padding-left: 0.75rem;
    padding-right: 0.75rem;

    h5 {
      font-size: 0.875rem;
    }

    p {
      font-size: 10px;
      line-height: 0.75rem;
    }

    .login_info {
      width: 300px;
      padding-left: 0.75rem;
      padding-right: 0.75rem;
      flex-direction: column;

      span {
        font-size: 10px;
        margin-left: 2px;
        margin-right: 2px;
      }
    }
  }
}
