.root {
  background-color: #2c2c2c;
  width: 420px;
  height: 400px;
  margin-bottom: 1.75rem;

  .image {
    width: 420px;
    height: 230px;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 500ms;

    &:hover {
      transform: scale(1.15);
      .image_overlay {
        background-image: linear-gradient(#f6004ea7 20%, #000);
      }
    }

    .image_overlay {
      background: linear-gradient(#ffffff00 30%, #000);
      width: 420px;
      height: 230px;
    }
  }

  @media screen and (max-width: 500px) {
    width: 300px;
    height: 365px;

    .image {
      width: 300px;
      height: 165px;

      .image_overlay {
        width: 300px;
        height: 165px;
      }
    }
  }
}
