.root {
  position: absolute;
  height: 0px;
  top: 100px;
  width: 100%;
  left: 0px;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  transition-duration: 300ms;
  overflow: hidden;

  &__scroll {
    top: 60px;
  }

  &__visible {
    height: 240px;
    opacity: 1;
    background-color: #2c2c2cc0;
  }

  .menu_nav {
    background-color: transparent;
    text-align: right;
    padding-right: 58px;
    padding-top: 0.5rem;

    li {
      background-color: transparent;
      margin-top: 18px;
      margin-bottom: 18px;
      position: relative;

      a {
        background-color: transparent;
        text-transform: uppercase;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        transition-duration: 150ms;
        transition-duration: 300ms;
      }

      &:hover {
        a {
          color: #f9004d;
        }
      }

      &::after {
        content: '';
        position: absolute;
        left: 0px;
        bottom: -0.4rem;
        height: 1px;
        width: 0px;
        background-color: #fff;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        transition-duration: 150ms;
        transition-duration: 300ms;
      }
    }

    .current_item {
      a {
        color: #b692f6;
      }

      &::after {
        color: #b692f6;
        width: 100%;
      }
    }
  }

  @media screen and (min-width: 850px) {
    display: none;
  }
}
