.root {
  height: 1400px;
  padding-left: 90px;
  padding-right: 90px;
  display: flex;
  align-items: center;

  .wrapper {
    width: 100%;

    .title {
      position: relative;
      margin-bottom: 5rem;

      h3 {
        text-transform: uppercase;
        font-weight: 900;
        font-size: 2.5rem;
        color: #b692f6;
        letter-spacing: 0.1em;
        margin-top: 5rem;
        text-align: right;
        padding-right: 10rem;

        span {
          background-color: transparent;
          letter-spacing: 0em;
          color: transparent;
          font-size: 150px;
          position: absolute;
          left: 0px;
          top: -7rem;
          opacity: 0.6;
          -webkit-text-stroke: 1px #cdd5df;
          -webkit-user-select: none;
        }
      }

      .line_1 {
        background-color: #b692f6;
        height: 0.125rem;
        position: absolute;
        top: 2rem;
        width: 920px;
      }

      .line_2 {
        background-color: #b692f6;
        height: 0.125rem;
        position: absolute;
        top: 2rem;
        right: 0px;
        width: 157px;
      }
    }

    .main_skills {
      display: flex;
      margin-top: 4rem;
      margin-bottom: 4rem;

      .main_skills_icon {
        padding: 30px;
        align-items: center;
        justify-content: center;
        display: flex;
        flex-basis: 25%;
        filter: grayscale(20%);
        transition-property: all;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        transition-duration: 150ms;
        transition-duration: 500ms;

        &:hover {
          transform: scale(1.1);
          filter: grayscale(0%);
        }

        img {
          max-width: 120px;
          max-height: 90px;
          cursor: pointer;
        }
      }
    }

    .other_skills {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      padding-left: 2.5rem;
      padding-right: 2.5rem;

      .other_skills_icon {
        padding: 0.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        transition-duration: 150ms;
        transition-duration: 500ms;
        margin-bottom: 0.75rem;
        background-color: transparent;

        flex-basis: 16%;
        filter: grayscale(100%);

        &:hover {
          transform: scale(1.4);
          filter: grayscale(0%);
        }

        img {
          max-width: 90px;
          max-height: 60px;
          cursor: pointer;
          border-radius: 0.5rem;
        }

        span {
          font-size: 1.5rem;
          font-family: 'Caveat', cursive;
        }
      }
    }
  }

  @media screen and (max-width: 1360px) {
    height: 1280px;

    .wrapper {
      .title {
        margin-top: -1.25rem;

        h3 {
          font-size: 2rem;

          span {
            font-size: 120px;
            top: -5rem;
          }
        }

        .line_1 {
          width: 100%;
        }
      }

      .other_skills {
        padding-left: 0px;
        padding-right: 0px;
      }
    }
  }

  @media screen and (max-width: 1149px) {
    height: 1240px;
    padding-left: 50px;
    padding-right: 50px;

    .wrapper {
      .title {
        h3 {
          font-size: 1.75rem;

          span {
            font-size: 100px;
            top: -4rem;
          }
        }
      }
    }
  }

  @media screen and (max-width: 920px) {
    height: 900px;

    .wrapper {
      .title {
        margin-top: 0px;

        h3 {
          font-size: 1.5rem;
          margin-top: 2.5rem;

          span {
            font-size: 80px;
            top: -2.75rem;
          }
        }
      }

      .main_skills {
        .main_skills_icon {
          padding: 25px;
          flex-basis: 21%;

          img {
            max-width: 100px;
            max-height: 75px;
          }
        }
      }

      .other_skills {
        .other_skills_icon {
          flex-basis: 14%;

          img {
            max-width: 70px;
            max-height: 40px;
          }

          span {
            font-size: 1rem;
          }
        }
      }
    }
  }

  @media screen and (max-width: 765px) {
    height: 980px;

    .wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        margin-bottom: 1.25rem;
        margin-top: 2.5rem;
        margin-right: -180px;

        h3 {
          font-size: 1.26rem;
          margin-top: 0px;

          span {
            font-size: 60px;
            top: -1.75rem;
            left: -250px;
          }
        }

        .line_1 {
          width: 200px;
          top: 1.5rem;
        }

        .line_1 {
          width: 100%;
        }
      }

      .main_skills {
        flex-wrap: wrap-reverse;
        max-width: 500px;
        align-items: center;
        justify-content: center;
        margin-top: 1.25rem;
        margin-bottom: 1.25rem;

        .main_skills_icon {
          flex-basis: 21%;
          padding: 25px;

          img {
            max-width: 100px;
            max-height: 75px;
          }
        }
      }

      .other_skills {
        padding-left: 0px;
        padding-right: 0px;

        .other_skills_icon {
          flex-basis: 14%;
          min-width: 100px;

          img {
            max-width: 70px;
            max-height: 40px;
          }

          span {
            font-size: 1rem;
          }
        }
      }
    }
  }

  @media screen and (max-width: 700px) {
    height: 1080px;

    .wrapper {
      .title {
        margin-top: 1.25rem;
      }
    }
  }

  @media screen and (max-width: 600px) {
    .wrapper {
      .title {
        margin-right: -140px;

        h3 {
          font-size: 1.12rem;
          margin-top: 0px;

          span {
            font-size: 50px;
            top: -1.25rem;
            left: -180px;
          }
        }
      }

      .main_skills {
        .main_skills_icon {
          flex-basis: 19%;
          padding: 20px;

          img {
            max-width: 90px;
            max-height: 65px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 500px) {
    height: 1360px;

    .wrapper {
      .title {
        margin-right: -140px;

        h3 {
          font-size: 0.875rem;

          span {
            font-size: 45px;
            top: -1rem;
            left: -155px;
          }
        }

        .line_1 {
          height: 1px;
          top: 1.25rem;
        }

        .line_2 {
          height: 1px;
          top: 0.75rem;
        }
      }
    }
  }

  @media screen and (max-width: 427px) {
    padding-left: 1.5rem;
    padding-right: 1.5rem;

    .wrapper {
      .title {
        margin-right: -100px;

        h3 {
          font-size: 0.75rem;

          span {
            font-size: 38px;
            top: -1rem;
            left: -105px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 374px) {
    height: 1420px;
  }
}
