.root {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 220px;
  justify-content: center;
  align-items: flex-start;
  background-color: transparent;
  gap: 2rem;

  a {
    display: flex;
    align-items: center;
    background-color: transparent;
    position: relative;
    margin-left: 6rem;

    svg {
      font-size: 1.12rem;
      fill: #cdd5df;
      margin-left: 0.5rem;
      background-color: transparent;
      transition-property: all;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transition-duration: 150ms;
      transition-duration: 300ms;
    }

    svg:first-child {
      font-size: 0.875rem;
    }

    span {
      font-size: 0.75rem;
      color: #cdd5df;
      margin-left: 0.5rem;
      background-color: transparent;
      transition-property: all;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transition-duration: 150ms;
      transition-duration: 300ms;
    }

    &:hover {
      svg {
        fill: #fff;
      }

      span {
        color: #fff;
      }
    }

    &::after {
      content: '';
      position: absolute;
      left: 0px;
      bottom: -0.375rem;
      height: 0.5px;
      width: 0px;
      background-color: #fff;
      transition-property: all;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transition-duration: 150ms;
      transition-duration: 300ms;
    }

    &:hover::after {
      width: 100%;
    }
  }

  @media screen and (max-width: 500px) {
    width: 300px;
    height: 160px;
    gap: 1.5rem;

    a {
      margin-left: 3.5rem;

      svg {
        font-size: 1rem;
        margin-left: 0.375rem;
      }

      svg:first-child {
        font-size: 0.75rem;
      }

      span {
        font-size: 11px;
        margin-left: 0.375rem;
      }
    }
  }
}
