.link {
  background-color: transparent;

  svg {
    fill: #697586;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 0.4s;
    background-color: transparent;
  }

  &:hover {
    svg {
      fill: #f9004d;
      transform: translateY(-2px);
    }
  }
}
