.root {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 4rem;
  background-color: transparent;

  .name {
    min-width: 175px;
    font-weight: 700;
    font-size: 25px;
    background-color: transparent;
  }

  .decor {
    display: flex;
    width: 100%;
    align-items: center;
    background-color: transparent;

    .or {
      background-color: transparent;
    }

    .line {
      height: 1px;
      margin-left: 1rem;
      margin-right: 1rem;
      background-color: #686666;
      width: 100%;
    }
  }

  @media screen and (max-width: 765px) {
    display: none;
  }
}
