.nav_toggle {
  background: none;
  width: 30px;
  font-size: 0;
  color: transparent;
  position: relative;
  cursor: pointer;
  border-style: none;
  z-index: 10;
  margin-top: 0.75rem;

  .nav_toggle__item {
    transition: background 0.3s linear;
    display: block;
    width: 100%;
    height: 3px;
    background-color: #e1dee6;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;

    &::before,
    &::after {
      content: '';
      transition: transform 0.3s linear;
      width: 100%;
      height: 3px;
      position: absolute;
      background-color: #e1dee6;
      left: 0px;
      z-index: 10;
    }

    &::after {
      bottom: -8px;
    }

    &::before {
      top: -8px;
    }
  }

  &__active {
    .nav_toggle__item {
      background: none;

      &::after {
        transform: rotate(-45deg) translate3d(0px, 3px, 0px);
        transform-origin: left bottom;
      }

      &::before {
        transform: rotate(45deg) translate3d(0px, -3px, 0px);
        transform-origin: left top;
      }
    }
  }

  &:focus {
    outline: 0;
  }
}
