.root {
  height: 1150px;
  background-color: #18181b;
  display: flex;
  align-items: center;
  padding: 0px 100px;

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -80px;
    width: 100%;
    background-color: transparent;

    .photo {
      width: 450px;
      min-width: 450px;
      border-radius: 0.5rem;
      background-color: transparent;

      img {
        width: 450px;
        height: 600px;
        border-radius: 0.5rem;
      }
    }

    .info_container {
      background-color: transparent;
      padding-right: 160px;

      .title {
        background-color: transparent;
        height: 240px;
        padding-top: 90px;
        position: relative;
        border-bottom-width: 1px;
        border-color: #cdd5dfcc;

        h3 {
          font-size: 2.5rem;
          text-transform: uppercase;
          color: #b692f6;
          font-weight: 900;
          letter-spacing: 0.1em;
          background-color: transparent;

          span {
            background-color: transparent;
            letter-spacing: 0em;
            color: transparent;
            font-size: 150px;
            position: absolute;
            left: 0px;
            top: -0.75rem;
            opacity: 0.6;
            -webkit-text-stroke: 1px #cdd5df;
            -webkit-user-select: none;
          }
        }

        p {
          background-color: transparent;
          margin-top: 1rem;
          letter-spacing: 0.1em;
        }
      }

      .info_text {
        background-color: transparent;
        margin-top: 2.5rem;

        p {
          line-height: 2rem;
          font-weight: 300;
          margin-bottom: 12px;
          background-color: transparent;
          font-family: 'Mulish', sans-serif;
        }
      }
    }
  }

  @media screen and (max-width: 1360px) {
    height: 1100px;
    padding-left: 80px;
    padding-right: 80px;

    .wrapper {
      margin-top: -1.25rem;

      .photo {
        width: 400px;
        min-width: 400px;

        img {
          width: 400px;
          height: 520px;
        }
      }

      .info_container {
        padding-right: 5rem;

        .title {
          height: 170px;
          padding-top: 60px;

          h3 {
            font-size: 2rem;

            span {
              font-size: 120px;
            }
          }
        }

        .info_text {
          margin-top: 1.25rem;
        }
      }
    }
  }

  @media screen and (max-width: 1100px) {
    height: 1150px;

    .wrapper {
      .photo {
        width: 350px;
        min-width: 350px;

        img {
          height: 450px;
          width: 350px;
        }
      }

      .info_container {
        padding-right: 3rem;

        .title {
          height: 140px;
          padding-top: 45px;

          h3 {
            font-size: 1.75rem;

            span {
              font-size: 100px;
            }
          }

          p {
            font-size: 0.875rem;
          }
        }

        .info_text {
          p {
            line-height: 1.75rem;
          }
        }
      }
    }
  }

  @media screen and (max-width: 920px) {
    height: 1500px;

    .wrapper {
      flex-direction: column;
      margin-top: 0px;
      align-items: flex-start;

      .photo {
        order: 1;
        width: 450px;
        min-width: 450px;

        img {
          width: 450px;
          height: 600px;
        }
      }

      .info_container {
        order: 2;
        padding-right: 1.25rem;

        .title {
          h3 {
            font-size: 2rem;
          }

          span {
            display: none;
          }

          p {
            margin-top: 0.5rem;
            font-size: 1.12rem;
          }
        }
      }
    }
  }

  @media screen and (max-width: 750px) {
    height: 1610px;
  }

  @media screen and (max-width: 620px) {
    padding-left: 50px;
    padding-right: 50px;
    height: 1550px;

    .wrapper {
      .photo {
        width: 350px;
        min-width: 350px;

        img {
          width: 350px;
          height: 460px;
        }
      }

      .info_container {
        .title {
          height: 125px;
          padding-top: 35px;

          h3 {
            font-size: 1.75rem;
          }

          p {
            margin-top: 0px;
            font-size: 1rem;
          }
        }
      }
    }
  }

  @media screen and (max-width: 520px) {
    height: 1420px;
    padding-right: 0px;

    .wrapper {
      .photo {
        min-width: 300px;
        width: 300px;

        img {
          height: 400px;
          width: 300px;
        }
      }

      .info_container {
        padding-right: 4rem;

        .title {
          height: 110px;
          padding-top: 30px;

          h3 {
            font-size: 1.5rem;
          }

          p {
            font-size: 0.875rem;
          }
        }

        .info_text {
          p {
            font-size: 0.875rem;
            line-height: 1.5rem;
            margin-bottom: 6px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 460px) {
    height: 1460px;
  }

  @media screen and (max-width: 400px) {
    padding-left: 40px;

    .wrapper {
      .info_container {
        padding-right: 2.5rem;
      }
    }
  }
}
