.root {
  position: absolute;
  z-index: 50;
  max-width: 1440px;
  width: 100%;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  transition-duration: 300ms;
  background-color: transparent;

  &__sticky {
    position: fixed;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #2c2c2cc0;
  }

  .wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 300ms;
    position: relative;

    &__sticky {
      padding-top: 0px;
      padding-bottom: 0px;
    }

    .menu_container {
      display: flex;
      align-items: center;
      height: 60px;
      background-color: transparent;

      svg {
        font-size: 2.5rem;
        background-color: transparent;
        animation: App-logo-spin infinite 20s linear;
      }
      .menu {
        background-color: transparent;

        .menu_nav {
          display: flex;
          margin-left: 2.5rem;
          background-color: transparent;

          .current_item {
            a {
              color: #fff;
            }

            &::after {
              width: 100%;
            }
          }

          li {
            margin-left: 1rem;
            margin-right: 1rem;
            position: relative;
            text-transform: capitalize;
            background-color: transparent;

            a {
              background-color: transparent;
            }

            &:hover {
              a {
                color: #fff;
              }
            }

            &::after {
              content: '';
              position: absolute;
              left: 0px;
              bottom: -0.5rem;
              height: 1px;
              width: 0px;
              background-color: #fff;
              transition-property: all;
              transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
              transition-duration: 150ms;
              transition-duration: 300ms;
            }

            &:hover::after {
              width: 100%;
              background-color: #b692f6;
            }
          }
        }
      }
    }

    .social_container {
      display: flex;
      gap: 1.5rem;
      align-items: center;
      background-color: transparent;

      a {
        font-size: 1.75rem;
      }
    }

    .burger_menu {
      background-color: transparent;
      height: 50px;
      width: 50px;
      display: none;
    }
  }

  @media screen and (max-width: 849px) {
    .wrapper {
      .burger_menu {
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          font-size: 2rem;
          background-color: transparent;
          margin-top: 0.375rem;
        }
      }

      .menu_container {
        .menu {
          display: none;
        }
      }
    }
  }

  @media screen and (max-width: 490px) {
    .wrapper {
      .social_container {
        display: none;
      }
    }
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
