.root {
  background-color: #05090fae;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  display: flex;
  height: 280px;
  width: 1440px;
  opacity: 0;
  left: 1440px;
  top: 400px;
  position: absolute;
  animation-duration: 1.5s, 1.5s;
  animation-delay: 0s, 8s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;

  &__shown {
    animation-name: show-in, show-out;
  }

  .icon {
    width: 150px;
    height: 150px;
    margin-bottom: 1.25rem;

    img {
      width: 100%;
      height: 100%;
      border-radius: 100px;
    }
  }

  .title {
    font-size: 1.26rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    margin-bottom: 0.5rem;
  }

  .message {
    font-size: 22px;
    letter-spacing: 0.05em;
  }
}

@keyframes show-in {
  0% {
    opacity: 0;
    left: 1440px;
  }

  30% {
    opacity: 0.7;
    left: 600px;
  }

  100% {
    opacity: 1;
    left: 0px;
  }
}

@keyframes show-out {
  0% {
    opacity: 1;
    left: 0px;
  }

  30% {
    opacity: 0.3;
    left: -500px;
  }

  100% {
    opacity: 0;
    left: -1440px;
  }
}
