.root {
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  z-index: 10;
  padding-top: 120px;
  padding-bottom: 120px;
  background: linear-gradient(145deg, #f9004d 30%, #b692f6);

  &::before {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: -10;
    opacity: 0.5;
    background-image: url('https://trydo.rainbowit.net/assets/images/pattern-1.png');
    content: '';
  }

  .link_container {
    text-align: center;
    background-color: transparent;

    h2 {
      background-color: transparent;
      font-size: 3rem;
      font-weight: 800;
      letter-spacing: 0.05em;
      margin-bottom: 2.5rem;
    }

    a {
      background-color: transparent;
      border-width: 2px;
      border-color: #e1dee6;
      padding-top: 0.75rem;
      padding-bottom: 0.75rem;
      padding-left: 4rem;
      padding-right: 4rem;
      transition-property: all;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transition-duration: 150ms;
      transition-duration: 300ms;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.1em;

      &:hover {
        background-color: #e1dee6;
        color: #f9004d;
      }

      &:active {
        transform: translateY(-2px);
      }
    }
  }

  @media screen and (max-width: 775px) {
    padding-top: 90px;
    padding-bottom: 90px;

    .link_container {
      h2 {
        font-size: 2rem;
        margin-bottom: 1.75rem;
      }

      a {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        padding-left: 3rem;
        padding-right: 3rem;
        font-size: 0.875rem;
      }
    }
  }

  @media screen and (max-width: 480px) {
    padding-top: 70px;
    padding-bottom: 70px;

    .link_container {
      h2 {
        font-size: 1.75rem;
        margin-bottom: 1.25rem;
      }

      a {
        padding-left: 2rem;
        padding-right: 2rem;
        font-size: 0.75rem;
      }
    }
  }
}
