.root {
  width: 100%;
  background-color: transparent;
  max-width: 550px;

  .input_container {
    background-color: transparent;
    height: 95px;

    label {
      display: block;
      margin-bottom: 0.25rem;
      background-color: transparent;
      color: #cdd5df;
    }

    input {
      background-color: transparent;
      border-width: 2px;
      border-color: #697586;
      padding-left: 0.75rem;
      padding-right: 0.75rem;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      width: 100%;

      &:focus-within {
        border-color: #e1dee6;
      }
    }
  }

  .textarea {
    background-color: transparent;
    height: 150px;
    margin-top: 1.25rem;
    position: relative;

    textarea {
      padding: 8px 12px;
      border-width: 2px;
      border-color: #697586;
      width: 100%;
      height: 100%;
      background-color: transparent;

      &:focus-within {
        border-color: #e1dee6;
      }
    }
  }

  .submit {
    margin-top: 1.25rem;
    font-size: 14px;
    letter-spacing: 2px;
    font-weight: 600;
    color: #cdd5df;
    border-width: 2px;
    border-color: #697586;
    width: 220px;
    height: 45px;
    position: absolute;
    right: 0px;
    bottom: -70px;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 300ms;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background-color: #e1dee6;
      border-color: #e1dee6;
      color: #18181b;
    }

    &:active {
      transform: translateY(-2px);
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  .error {
    letter-spacing: 0.05em;
    font-size: 0.75rem;
    background-color: transparent;
    color: #f9004d;
  }

  @media screen and (max-width: 765px) {
    max-width: 750px;
  }

  @media screen and (max-width: 500px) {
    .submit {
      width: 175px;
      font-size: 0.875rem;
    }
  }
}
