.wrapper {
  background-color: #18181b;

  .root {
    height: 1040px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 100px;
    padding-right: 100px;
    background-color: #18181b;

    .title {
      position: relative;
      margin-top: -1.25rem;
      margin-bottom: 10rem;
      background-color: transparent;

      h3 {
        text-transform: uppercase;
        font-weight: 900;
        background-color: transparent;
        font-size: 2.5rem;
        color: #b692f6;
        letter-spacing: 0.1em;
        padding-left: 10rem;
        margin-bottom: -0.25rem;
      }

      span {
        letter-spacing: 0em;
        color: transparent;
        font-size: 150px;
        left: 90px;
        opacity: 0.6;
        top: -4rem;
        position: absolute;
        background-color: transparent;
        -webkit-text-stroke: 1px #cdd5df;
        -webkit-user-select: none;
      }

      div {
        background-color: #b692f6;
        height: 0.125rem;
      }
    }

    .main_container {
      display: flex;
      justify-content: space-between;
      width: 100%;
      background-color: transparent;

      .contacts_inner {
        background-color: transparent;
        margin-top: 2.25rem;

        .contact {
          background-color: transparent;
          text-align: right;
          margin-bottom: 60px;

          span {
            background-color: transparent;
            font-size: 1.12rem;
            color: #cdd5df;
          }

          span:first-of-type + span {
            margin-left: 0.75rem;
            margin-right: 0.75rem;
            font-weight: 600;
            text-transform: uppercase;
          }

          svg {
            font-size: 2rem;
            fill: #cdd5df;
            background-color: transparent;
            display: inline-block;
          }
        }
      }
    }

    @media screen and (max-width: 1360px) {
      .title {
        h3 {
          span {
            left: 50px;
          }
        }
      }

      .main_container {
        .contacts_inner {
          margin-top: 0.5rem;
          margin-left: 2.5rem;

          .contact {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.5rem;
            margin-bottom: 40px;

            span {
              order: 3;
            }

            span:first-of-type + span {
              order: 2;
            }

            svg {
              order: 1;
            }
          }
        }
      }
    }

    @media screen and (max-width: 1220px) {
      height: 1100px;

      .title {
        h3 {
          font-size: 2rem;

          span {
            font-size: 120px;
          }
        }
      }
    }

    @media screen and (max-width: 950px) {
      height: 1000px;
      padding-left: 50px;
      padding-right: 50px;

      .title {
        margin-left: -320px;
        margin-bottom: 7rem;
      }

      .main_container {
        .contacts_inner {
          .contact {
            flex-direction: column;
            display: flex;
            align-items: center;
            gap: 0.5rem;
            margin-bottom: 40px;

            span {
              font-size: 1rem;
            }

            svg {
              font-size: 1.75rem;
            }
          }
        }
      }
    }

    @media screen and (max-width: 765px) {
      .title {
        margin-bottom: 5rem;

        h3 {
          font-size: 1.75rem;

          span {
            font-size: 100px;
          }
        }
      }

      .main_container {
        display: flex;
        flex-direction: column;

        form {
          order: 3;
        }

        .contacts_inner {
          display: flex;
          justify-content: space-between;
          width: 100%;
          margin-left: 0px;
          margin-bottom: 5rem;
          order: 1;
          margin-top: 0px;

          .contact {
            margin-bottom: 0px;
          }
        }
      }
    }

    @media screen and (max-width: 650px) {
      height: 920px;

      .title {
        margin-bottom: 7rem;

        h3 {
          font-size: 1.12rem;

          span {
            font-size: 75px;
            left: 120px;
            top: -2rem;
          }
        }
      }

      .main_container {
        .contacts_inner {
          .contact {
            span {
              font-size: 0.75rem;
            }
          }
        }
      }
    }

    @media screen and (max-width: 550px) {
      height: 940px;

      .main_container {
        .contacts_inner {
          display: block;
          margin-bottom: 2.5rem;

          .contact {
            display: block;
            margin-bottom: 2.5rem;
          }

          .contact:last-child {
            margin-bottom: 0px;
          }
        }
      }
    }

    @media screen and (max-width: 480px) {
      height: 1050px;

      .title {
        margin-bottom: 5rem;
        margin-left: -250px;
        margin-top: -3rem;

        h3 {
          font-size: 1rem;

          span {
            font-size: 55px;
            left: 120px;
            top: -1.5rem;
          }
        }
      }

      .main_container {
        .contacts_inner {
          .contact {
            display: flex;
          }
        }
      }
    }
  }
}
