.root {
  background-color: transparent;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  .technologies {
    font-size: 12px;
    width: 100%;
    text-align: center;
    background-color: #10132358;
    font-weight: 700;
  }

  @media screen and (max-width: 500px) {
    .technologies {
      font-size: 8px;
    }
  }
}
