.root {
  height: 700px;
  min-height: 700px;
  padding-top: 100px;
  padding-left: 80px;

  .main_container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    position: relative;

    .photo {
      min-width: 600px;
      height: 600px;
      width: 600px;
      background: linear-gradient(
          to left,
          rgb(5, 9, 15),
          rgb(5, 9, 15),
          rgba(5, 9, 15, 0.445),
          rgba(255, 255, 255, 0),
          rgba(255, 255, 255, 0),
          rgba(255, 255, 255, 0),
          rgba(5, 11, 20, 0.546),
          rgb(5, 11, 20),
          rgb(3, 7, 12)
        ),
        center 0% / 75% 100% no-repeat url(../../assets/img/main.png);
    }

    .title_inner {
      position: absolute;
      top: 180px;
      right: 140px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: transparent;

      .first_name {
        color: transparent;
        background-color: transparent;
        line-height: 6rem;
        letter-spacing: 0.1em;
        font-weight: 900;
        text-transform: uppercase;
        font-size: 100px;
        -webkit-text-stroke: 1px #b692f6;
      }

      .last_name {
        font-weight: 900;
        letter-spacing: 0.1em;
        line-height: 6rem;
        text-transform: uppercase;
        font-size: 100px;
        background: -webkit-linear-gradient(-45deg, #fbfaff 25%, #b692f6);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }

      p {
        letter-spacing: 0.3em;
        background-color: transparent;
        margin-top: 0.5rem;
      }
    }
  }

  @media screen and (max-width: 1330px) {
    .main_container {
      .title_inner {
        right: 50px;
      }
    }
  }

  @media screen and (max-width: 1100px) {
    .main_container {
      .title_inner {
        .first_name {
          font-size: 70px;
          line-height: 4rem;
        }

        .last_name {
          font-size: 70px;
          line-height: 4rem;
        }
      }
    }
  }

  @media screen and (max-width: 850px) {
    padding-left: 0px;
    height: 600px;
    min-height: 600px;

    .main_container {
      .photo {
        min-width: 400px;
        height: 400px;
        width: 400px;
      }

      .title_inner {
        right: 40px;

        .first_name {
          font-size: 55px;
          line-height: 3rem;
        }

        .last_name {
          font-size: 55px;
          line-height: 3rem;
        }
      }
    }
  }

  @media screen and (max-width: 550px) {
    .main_container {
      .photo {
        min-width: 310px;
        width: 310px;
        height: 400px;
        background: linear-gradient(
            to left,
            rgb(5, 9, 15),
            rgb(5, 9, 15),
            rgba(5, 9, 15, 0.445),
            rgba(255, 255, 255, 0),
            rgba(255, 255, 255, 0),
            rgba(255, 255, 255, 0),
            rgba(5, 11, 20, 0.546),
            rgb(5, 11, 20),
            rgb(3, 7, 12)
          ),
          center 0% / 100% 100% no-repeat url(../../assets/img/main.png);
      }

      .title_inner {
        right: 20px;

        .first_name {
          font-size: 45px;
          line-height: 2.5rem;
        }

        .last_name {
          font-size: 45px;
          line-height: 2.5rem;
        }

        p {
          font-size: 0.875rem;
          margin-top: 0.25rem;
        }
      }
    }
  }

  @media screen and (max-width: 500px) {
    padding-left: 0px;
    height: 660px;
    min-height: 660px;

    .main_container {
      .title_inner {
        top: 420px;
        right: 90px;
      }
    }
  }

  @media screen and (max-width: 400px) {
    .main_container {
      .title_inner {
        right: 50px;
      }
    }
  }
}
