.wrapper {
  background-color: #18181b;

  .root {
    background-color: #18181b;
    height: 2380px;
    padding-top: 120px;
    padding-left: 2.5rem;
    padding-right: 2.5rem;

    .title {
      position: relative;
      margin-top: 2.5rem;
      margin-bottom: 10rem;
      background-color: transparent;

      h3 {
        text-transform: uppercase;
        font-weight: 900;
        background-color: transparent;
        font-size: 2.5rem;
        color: #b692f6;
        letter-spacing: 0.1em;
        padding-left: 10rem;
        margin-bottom: -0.25rem;
      }

      span {
        background-color: transparent;
        letter-spacing: 0em;
        color: transparent;
        font-size: 150px;
        position: absolute;
        left: 90px;
        top: -4rem;
        opacity: 0.6;
        -webkit-text-stroke: 1px #cdd5df;
        -webkit-user-select: none;
      }

      div {
        background-color: #b692f6;
        height: 0.125rem;
      }
    }

    .projects_container {
      background-color: transparent;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 2.5rem;
    }

    @media screen and (max-width: 1419px) {
      height: 2850px;
      padding-top: 110px;
    }

    @media screen and (max-width: 1100px) {
      padding-top: 100px;
      height: 2820px;

      .title {
        h3 {
          font-size: 2rem;

          span {
            font-size: 120px;
            top: -3.5rem;
          }
        }
      }
    }

    @media screen and (max-width: 959px) {
      height: 5080px;
      padding-top: 80px;

      .title {
        margin-bottom: 7rem;

        h3 {
          font-size: 1.5rem;

          span {
            font-size: 100px;
            top: -3rem;
          }
        }
      }
    }

    @media screen and (max-width: 775px) {
      height: 5020px;
      padding-top: 60px;

      .title {
        margin-bottom: 7rem;

        h3 {
          font-size: 1.26rem;

          span {
            font-size: 80px;
            top: -2.25rem;
          }
        }
      }
    }

    @media screen and (max-width: 650px) {
      height: 4960px;

      .title {
        margin-bottom: 5rem;

        h3 {
          padding-left: 5rem;
          font-size: 1.12rem;

          span {
            font-size: 60px;
            left: 40px;
            top: -1.75rem;
          }
        }
      }
    }

    @media screen and (max-width: 500px) {
      height: 4540px;
      padding-left: 1.25rem;
      padding-right: 1.25rem;

      .title {
        margin-top: 1.25rem;

        h3 {
          padding-left: 5rem;
          font-size: 1rem;

          span {
            font-size: 48px;
            left: 20px;
            top: -1.25rem;
          }
        }
      }
    }
  }
}
